body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #FAFAFA;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  overflow-x: scroll;
}

p {
  color: #000;
  font-size: 2rem;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: auto;
  position: relative;
  margin: 0 auto;
}

.contenido-centrado {
  max-width: 80rem;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 2rem;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.tabla__color {
  background-color: #fff;
}

.bg-login {
  background-color: #5C6273;
}

.contenido__formulario {
  background-color: #fff;
  border-radius: 2rem;
  padding: 3rem;
}

.contenido__login {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2;
  margin-top: 15rem;
}
.contenido__login .formulario {
  padding: 5rem;
}
.contenido__login .formulario input {
  margin-top: 2rem;
  width: 100%;
  height: 3rem;
  border: 1px solid #324BD9;
  border-radius: 1rem;
}
.contenido__login .contenido__imagen {
  max-width: 70%;
  margin: 0 auto;
}
.contenido__login legend {
  font-size: 5rem;
  text-transform: uppercase;
  color: #212940;
  display: block;
  text-align: center;
  font-weight: 700;
  margin-top: -4rem;
}

.formulario__submit {
  width: 30%;
  position: relative;
}
.formulario__submit .formulario__submit--cambio {
  background-color: #212940;
  color: #fff;
  margin-top: 1rem;
  position: absolute;
  top: 1rem;
}
.formulario__submit .formulario__submit--cambio:hover {
  cursor: pointer;
  background-color: #324BD9;
  align-items: center;
}

.header {
  display: grid;
  grid-template-columns: 3;
  grid-template-rows: 0;
  background-color: #4E63D9;
  padding: 5rem 0;
  height: 5rem;
  margin-bottom: 2rem;
}
.header .logo {
  grid-column: 1/2;
  width: 10rem;
  height: 10rem;
  margin-left: 5rem;
}
.header .logo a picture img {
  border-radius: 50rem;
}
.header .navegacion {
  grid-column: 2/3;
  margin: -4rem auto 0 auto;
}
.header .navegacion ul {
  position: relative;
  width: auto;
  display: flex;
  padding: 0;
}
.header .navegacion ul li {
  list-style: none;
  display: block;
  margin-right: 3rem;
}
.header .navegacion ul li ul {
  position: absolute;
  display: none;
  width: auto;
  background-color: #4E63D9;
}
.header .navegacion ul li ul li {
  margin: 0;
  display: block;
}
.header .navegacion ul li a {
  color: #fff;
  text-decoration: none;
  display: block;
  align-items: center;
  padding: 1rem;
  border-radius: 0.5rem;
  width: 100%;
  cursor: pointer;
}
.header .navegacion ul li a:hover {
  transition: all 0.3s;
  color: #4E63D9;
  background-color: #fff;
}
.header .navegacion ul li a .icono {
  margin-right: 1rem;
  font-size: 2rem;
}
.header .header__info {
  position: relative;
  grid-column: 3/4;
  display: flex;
  align-content: center;
  justify-content: center;
}
.header .header__info .info-sesion p {
  margin-top: -1rem;
  color: #fff;
  font-weight: 400;
}
.header .header__info .cerrar-sesion {
  margin-top: -4rem;
  position: absolute;
  right: 2rem;
  top: -1rem;
  margin-bottom: 1rem;
}
.header .header__info .cerrar-sesion a {
  color: #fff;
  font-family: "Lato", sans-serif;
  text-decoration: none;
  font-size: 1.5rem;
  margin-left: 5rem;
}
.header .header__info .cerrar-sesion a:hover {
  font-weight: 700;
}
.header .header__info .cerrar-sesion .icono {
  font-size: 2rem;
}
.header .header__info p {
  margin-top: 1rem;
  color: #fff;
  font-family: "Lato", sans-serif;
  font-weight: 900;
  text-decoration: none;
  font-size: 2rem;
  line-height: 1;
}

.navegacion ul li:hover ul {
  z-index: 1000;
  display: inline-block;
  transition: all 0.3s;
  margin: 0;
  border-radius: 0.5rem;
}

.footer {
  background-color: #4E63D9;
  padding: 3rem 0;
  width: 100%;
  margin-top: auto;
}
.footer p {
  font-weight: 400;
  color: #fff;
  text-align: center;
  text-transform: initial;
}
.footer p span {
  color: #fff;
  font-size: 1.5rem;
}

.buscador {
  transform: translateY(5rem);
  position: fixed;
}
.buscador input[type=text] {
  height: 3rem;
  width: 25rem;
}
.buscador i {
  position: relative;
  right: 3rem;
  width: 2rem;
  height: 2rem;
}
.buscador i:hover {
  cursor: pointer;
}
.buscador .buscar {
  transform: translateX(-11.5rem);
  background-color: transparent;
  border: none;
  padding-right: 3rem;
}

.buscar:hover, i {
  color: #00d702;
  cursor: pointer;
}

.tabla {
  width: 100%;
  border: 1px solid #000;
  margin: 8rem 0 0 0;
  border-collapse: collapse;
  min-height: calc(100% - 4rem);
  margin-top: 10rem;
}
.tabla thead {
  background-color: #4E63D9;
}
.tabla thead th {
  color: #fff;
}
.tabla thead,
.tabla tbody,
.tabla th,
.tabla td {
  text-align: center;
  border: 1px solid #000;
  padding: 0 1rem;
}
.tabla .boton {
  display: block;
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 12rem;
  height: 5rem;
  color: #000;
  font-weight: 500;
  font-family: "Lato", sans-serif;
  font-size: 2rem;
  border-radius: 2rem;
  text-decoration: none;
  text-align: center;
  border: 1px solid #5C6273;
  height: 3rem;
}
.tabla .boton:hover {
  cursor: pointer;
}
.tabla .boton-eliminar {
  background-color: red;
}
.tabla .boton-actualizar {
  background-color: #00d702;
}
.tabla .resultado-imagen {
  width: 10rem;
  height: 10rem;
  margin: auto;
}
.tabla .resultado-imagen.resultado-imagen:hover {
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  display: grid;
  place-items: center;
  background-color: rbg(0 0 0/0.5);
}
.overlay img {
  display: block;
  max-width: 60rem;
  max-height: 60rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #4E63D9;
  color: #000;
  display: grid;
  place-items: center;
  font-weight: 700;
  margin-top: -15rem;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.imagen-small {
  width: 15rem;
}

.orden .bloque {
  align-items: center;
}

.orden .ancho {
  width: 40%;
}

.datos__registros p {
  color: #000;
}

.paginador {
  margin: 5rem auto 0 auto;
}
.paginador .anterior {
  margin-right: 0.5rem;
}
.paginador .anterior a {
  text-decoration: none;
  color: #000;
}
.paginador .pagina a {
  margin-right: 0.5rem;
  text-decoration: none;
  color: #000;
}
.paginador .siguiente a {
  text-decoration: none;
  color: #000;
}

.datos__registros span {
  font-weight: 900;
  margin-left: 1rem;
}

.datos__registros p {
  font-size: 1.5rem;
  font-weight: 400;
}

.accion {
  margin: 0 auto;
}

.accion form input, a {
  margin: auto auto;
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10rem;
  background-color: #FF0000;
  padding: 1rem;
  color: #000;
  font-weight: 400;
  border-radius: 50rem;
}

.formulario {
  margin-top: 5rem;
}

fieldset {
  text-transform: uppercase;
}

.orden {
  display: flex;
  padding: 0 5rem;
}
.orden label {
  width: 20%;
  margin-bottom: 2rem;
  text-transform: initial;
}
.orden input {
  width: 90%;
  margin-bottom: 2rem;
}
.orden textarea {
  width: 90%;
}

.error p {
  color: #000;
}